
.photoContainer {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin: 0 40px 0 0;
}
.photo {
  & img {
    max-width: 100%;
    max-height: 100%;
  }
}
.uploadPhoto {
  max-width: 100%;
  display: flex;
  justify-content: flex-end;
  font-size: 12px;
  font-weight: bold;
  padding: 0;
  & .input_file {
    width: .1px;
    height: .1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
    & +label{
      display: block;
      cursor: pointer;
&:hover{
  text-shadow:3px 3px 6px rgba(0,0,0,0.5) ;
}
    }
  }
}
